<template>
  <div>
    <form @submit.prevent="performLogin" method="post">
      <p>
        <label for="username">Username: </label><br>
        <input v-model="username" type="text" name="username" id="username" required="required">
      </p>

      <p>
        <label for="password">Password: </label><br>
        <input v-model="password" type="password" name="password" id="password" required="required">
      </p>

      <p>
        <input type="submit" value="Login">
      </p>
    </form>
  </div>
</template>

<script>
export default {
  name: "Login",
  data:() => ({
    username: '',
    password: '',
  }),
  methods: {
    performLogin() {
      let formData = new FormData();
      formData.set("username", this.username.trim());
      formData.set("password", this.password.trim());
      fetch('http://localhost:8080/login', {
        method: 'post',
        //credentials: 'include',
        headers: {
          //'X-XSRF-TOKEN': getCsrf(uri),
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: formData
      }).then((result) => {
        console.log(result.json());
        localStorage.setItem('username', this.username.trim());
      }).catch((error) => {
            console.error(error)
      })
    }
  }
}
</script>